* {
  box-sizing: border-box;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

body {
  margin: 2rem;
  background-image: url(/portfolio-leah/static/media/Chicago_skyline_kz03.a1b89608.jpg);
  background-size: cover;
}

.about {
  width: 25vw;
}
.nav {
    background-image: linear-gradient(rgb(136, 183, 181), rgb(203, 230, 249));
    color: black;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    grid-gap: 2rem;
    gap: 2rem;
    padding: 0 1rem;
    height: 12vh;
}

.site-title {
    font-size: 2rem;
    font-weight: bold;
}

.nav ul {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}

.nav a {
    color: inherit;
    text-decoration: none;
    display: flex;
    align-items: center;
    height: 100%;
    padding: .25rem;
}

.nav li:hover {
    background-color: rgb(167, 202, 177);
    box-shadow: 2px 3px rgb(136, 183, 181);
}

.nav li:active {
    background-color: rgb(244, 236, 214);
}

.footer {
    color: rgb(244, 236, 214);
    padding: 5px;
    position: absolute;
    bottom: 0;
    width: 100%;
    align-items: center;
    vertical-align: middle;
}
.footer h6 {
    margin: 0;
}
.card {
    margin: 2rem;
}
.resume {
    background-image: radial-gradient(rgb(167, 202, 177), rgb(136, 183, 181), rgb(203, 230, 249));
}

h2 {
    padding: 2rem 0;
}
